body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Brandon", "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image:url(/static/media/bg_color.c2c1aefd.svg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.d-flex {
  display: flex;
  width: 100%;
}

.fw{
  flex-wrap: wrap;
}

.jcc {
  justify-content: center;
}

.aic {
  align-items: center;
}
/* Landing */

.home-landing {
  width: 100%;
  height: calc(100vh - 100px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.home-landing img {
  width: 400px;
  height: auto;
}

.home-landing h3, h5 {
  color: #989899;
}

.footer {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.social {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.social img {
  margin: 20px 5px 0 5px;
  max-height: 30px;
  width: auto;
  height: 100%;
}

.home-carousel {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

/* Objetivos */

.objetivos {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.obj-header {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
}

.obj-container {
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 6;
  justify-content: center;
  align-content: center;
}

.obj-container img {
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Video */
video {
  width: auto;
  height: 600px;
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.video-container {
  width: 100%;
  height: 100%;
}
.auth {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.auth-container {
  width: 100%;
  height: 100%;
}

.auth-form h2 {
  margin-top: 50px;
}

.radio-files-container {
  width: 100%;
  height: 100%;
  margin: 20px 0;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.radio-container {
  width: 45%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-content: space-between;
  align-items: center;
  text-align: center;
  color: white;
}

.files-container {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-around;
  align-items: center;
  text-align: center;
  color: white;
}

.files-container input, button {
  width: 400px;
}

.user-container, .info-container {
  width: 45%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: space-around;
  align-items: center;
  text-align: center;
  color: white;
}

.user-container input, button {
  width: 400px;
}

.login-form {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
}

.login-form input {
  width: 400px;
}

.login-form button {
  width: 100px;
}
/* Objetivos */

.objetivos {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.obj-header {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
}

.obj-container {
  margin: 10px;
  display: flex;
  flex-wrap: wrap;
  flex-grow: 6;
  justify-content: center;
  align-content: center;
}

.obj-container img {
  margin: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

